<template>
  <AntTabs :active-key="routeName" :options="segmentedOptions" @update:active-key="goTo">
    <AntTabPane v-for="option in segmentedOptions" :key="option.value" :name="option.value">
      <template #tab>
        <span>{{ option.label }}</span>
      </template>
    </AntTabPane>
  </AntTabs>
</template>

<script lang="ts" setup>
import { TabPane as AntTabPane, Tabs as AntTabs } from 'ant-design-vue';
import { Key } from 'ant-design-vue/es/_util/type';
import { useRoute, useRouter } from 'vue-router';

const router = useRouter();
const route = useRoute();
const routeName = String(route.name);
const projectId = route.params.projectId as string;

const segmentedOptions = [
  { label: 'Tables list', value: 'tables' },
  { label: 'Diagram view', value: 'tables-diagram' },
  { label: 'Run SQL', value: 'sql' },
] as const;

const goTo = (name: Key) => {
  router.push({
    name: String(name),
    params: {
      projectId,
    },
  });
};
</script>
